.siteheader {
  font-family: arial;
  font-size: 300%;
  color: #323fef;
  margin-top: 135px;
  font-weight: bold;
}

.subheadertext {
  font-family: arial;
  font-size: 80%;
  color: #7c7c7c;
  margin-top: -25px;
}

.navbutton {
  padding-top: 9px;
  padding-bottom: 9px;
  background: #323fef;
  border: 1px solid #323fef;
  color: white;
  width: 100px;
  float: center;
  text-align: center;
  font-size: 12px;
  font-weight: bold;
  border-radius: 0;
  font-family: arial;
  vertical-align: middle;
  margin-bottom: 10px;
  margin-top: 25px;
  display: inline-block;
  cursor: pointer;
  margin-right: 2px;
  /* box-shadow: 2px 2px #c8c8c8; */
}

.navbutton:hover {
  background: #5964f8;
  color: white;
  font-weight: bold;
  border: 1px solid #5964f8;
}

.sitebody {
  background-color: white;
  font-family: arial;
  padding: 30px 40px;
  color: #323fef;
  text-align: center;
  font-size: 65%;
}

input {
  background: #f7f7f7;
  /*box-shadow: 2px 2px 0.5px 0.5px #c8c8c8;*/
  font: arial;
  margin: 0;
  border: 1px solid #c8c8c8;
  border-radius: 0;
  width: 300px;
  padding: 7px;
  float: center;
  font-size: 16px;
  vertical-align: middle;
  margin-right: 10px;
}

input:focus {
  background-color: white;
  outline-style: none;
}

.button {
  font-family: arial;
  background: #323fef;
  border: 1px solid #323fef;
  color: white;
  padding: 9px;
  margin-right: 2px;
  width: 50px;
  font-size: 12px;
  float: center;
  text-align: center;
  font-weight: bold;
  cursor: pointer;
  transition: 0.3s;
  border-radius: 0;
  vertical-align: middle;
  display: inline-block;
}

.button:hover {
  background: #5964f8;
  border: 1px solid #5964f8;
}

ul li {
  font-family: arial;
  cursor: pointer;
  position: center;
  list-style: none;
  padding: 10px 10px 10px 10px;
  margin-right: 25px;
  background: #dbeaf8;
  color: #3e3e3e;
  font-size: 13px;
  transition: 0.2s;
  margin-bottom: 5px;
  width: 50%;
}

.tasklist {
  font-family: arial;
  font-size: 90%;
  color: #7c7c7c;
  margin-top: 25px;
}

.clearbutton {
  font-family: arial;
  background: #323fef;
  border: 1px solid #323fef;
  color: white;
  padding: 9px;
  margin-right: 2px;
  width: 100px;
  font-size: 12px;
  float: center;
  text-align: center;
  font-weight: bold;
  cursor: pointer;
  transition: 0.3s;
  border-radius: 0;
  vertical-align: middle;
  display: inline-block;
}

.clearbutton:hover {
  background: #5964f8;
  border: 1px solid #5964f8;
}

.about {
  font-family: arial;
  cursor: pointer;
  position: center;
  list-style: none;
  padding: 25px 25px 25px 25px;
  margin-right: 25px;
  background: #dbeaf8;
  color: #3e3e3e;
  font-size: 13px;
  transition: 0.2s;
  margin-top: 45px;
  margin-bottom: 5px;
  width: 70%;
}
